import {React, useState} from 'react'
import styled from 'styled-components'
import {addToCart, removeFromCart} from "./CartUtils"
import Card from './ProductCard';

export default function Checkout({props}){
    const [cart, setCart] = useState(localStorage.getItem("MyCart") ? JSON.parse(localStorage.getItem("MyCart")) : []);

    const removeElem = (elem) =>{
        removeFromCart(elem);
        setCart(localStorage.getItem("MyCart") ? JSON.parse(localStorage.getItem("MyCart")) : [])
    };
    const edit = (elem,qty) => {
        addToCart(elem,qty);
        setCart(localStorage.getItem("MyCart") ? JSON.parse(localStorage.getItem("MyCart")) : [])
    }

    return(<>
        {cart.map((id,index) => <>
            <Card  props={{"id":id,"remove":removeElem, "edit":edit}} key={index} />
        </>)}
    </>);
}